import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import Web3 from 'web3'
import BigNumber from 'bignumber.js'
import { formatEth, ethToWei } from './utils/ethUtils'

const web3 = new Web3()

Vue.use(Vuex)

function getFormat(data, decimal) {
  if (!data) { return 0 }
  if (data.toString() === '0') { return 0 }
  if (decimal === 0) {
    return BigNumber(data / 100).toFixed(decimal)
  }
  return BigNumber(web3.utils.fromWei(data.toString())).toFixed(decimal)
}

function getBackFormat(data, decimal) {
    if (!data) { return 0 }
    if (data.toString() === '0') { return 0 }
    if (decimal === 2) {
        return BigNumber(data / 100).toFixed(decimal)
    }
    return BigNumber(web3.utils.fromWei(data.toString())).toFixed(decimal)
}

async function getStepIndex(contract) {
  return contract.methods.getCurrentStepFundsInfo().call()
}

export default new Vuex.Store({
  state: {
    account: '',
    balance: 0,
    isWait: false,
    wait: true,
    gameInfo: {},
    userInfo: {},
  },
  mutations: {
    GET_GAME_INFO: (state, data) => {
      state.gameInfo = data
    },
    GET_USER_INFO: (state, data) => {
      state.userInfo = data
    },
    GET_BALANCE: (state, data) => {
      state.balance = data
    },
    GET_IS_WAIT: (state, data) => {
      state.isWait = data
    },
    GET_WAIT: (state, data) => {
      state.wait = data
    }
  },
  actions: {
    async getBalance({ commit }, account) {
      window.web3.eth.getBalance(account)
      .then(balance => {
        commit('GET_BALANCE', getFormat(balance, 4))
      }).catch(err => {
        console.log(err)
      })
    },
    async getGameInfo({ commit }, contract) {
      const self = this
      contract.methods.getGameInfo().call({from: this.state.account})
      .then(res => {
        if (res) {
          console.log('getGameInfo', res)
          const data = {}
          data.rid = res[0]
          data.uid = res[1]
          data.startTime = res[2]
          data.investCount = res[3]
          data.investMoney = getFormat(res[4], 4)
          data.rInvestCount = res[5]
          data.rInvestMoney = [6],
          data.coefficient = res[7]
          self.dispatch('getUserInfo', [contract, res[0]])
          console.log('get game info', data)
          commit('GET_GAME_INFO', data)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    async getUserInfo({ commit }, params) {
      const [contract, rid] = params
      contract.methods.getUserInfo(this.state.account, rid).call()
      .then(res => {
        const data = []
        data[0] = res.inviteCode
        data[1] = res.referrer
        data[2] = res.ct[1]
        data[3] = getFormat(res.ct[3], 4)
        data[4] = res.ct[2]
        data[5] = res.ct[10]

        // 累计投注分红 + 累计节点分红
        data[6] = getFormat(res.ct[8] + res.ct[9], 4)
        data[7] = getFormat(res.ct[4], 4)
        // 可提现数量 = 解锁数量[5]
        data[8] = getFormat(res.ct[5], 4)
        data[9] = getFormat(res.ct[8], 4)
        data[10] = getFormat(res.ct[9], 4)
        // 待提现收益 = 总静态 + 累计节点
        data[11] = getFormat(res.ct[6] + res.ct[9], 4)
        console.log('getUserInfo', res.ct)
        commit('GET_USER_INFO', data)
      }).catch(err => {
        console.log(err)
      })
    },

    async withdrawProfit({ commit }) {
      const self = this
      window.contract.methods.withdrawProfit()
        .send({ from: this.state.account })
        .then(res => {
          console.log('withdrawProfit ', res)
        }).catch(err => {
          console.log(err)
          self._vm.$alert('Metamask 提交失败', '提示', {
            confirmButtonText: '确定'
          })
        })
    },
    async redeem({ commit }) {
      const self = this
      window.contract.methods.redeem()
        .send({ from: this.state.account })
        .then(res => {
          console.log('redeem ', res)
        }).catch(err => {
          console.log(err)
          self._vm.$alert('Metamask 提交失败', '提示', {
            confirmButtonText: '确定'
          })
        })

    },
    async isCode({ commit }, inviteCode) {
      return window.contract.methods.isCode(inviteCode).call()
    },
    async getWaitInfo({ commit }, contract) {
      contract.methods.getWaitInfo(this.state.account).call()
      .then(res => {
        console.log('getWaitInfo', res)
        commit('GET_IS_WAIT', res.isWait)
        commit('GET_WAIT', res.wait)
      }).catch(err => {
        console.log(err)
      })

    },
    async waitInvest({ commit }, data) {
      console.log(data)
      const inviteCode = data.inviteCode
      const referer = data.referer
      const value = data.value
      const self = this
      console.log(inviteCode, referer, this.state.account, value)
      window.contract.methods.waitInvest(inviteCode, referer)
        .send({
          from: this.state.account,
          to: window.contract.address,
          value: web3.utils.toWei(String(value))
        })
        .then(res => {
          console.log('waitIndest success', res)
        }).catch(err => {
          console.log(err)
          commit('GET_IS_WAIT', false)
          self._vm.$alert('Metamask 提交失败', '提示', {
            confirmButtonText: '确定'
          })
        })
      commit('GET_IS_WAIT', true)
    },
    async depositETH({ commit }, amount) {
      const self = this
      const data = {
        from: self.state.account,
        to: window.contract.address,
        value: web3.utils.toWei(amount),
        gas: '0x52080'
      }
      window.web3.eth.sendTransaction(data)
      .then(res => {
        console.log(res)
      }).catch(err => {
        self._vm.$alert('Metamask 提交失败', '提示', {
          confirmButtonText: '确定',
        })
      })
   },
   async startListenEvent({commit}, data) {
      const contract = data.contract
      const eventName = data.eventName
      contract.events[eventName]({}, (error, event) => {
        console.log(eventName, 'initial event: ', event, event.returnValues)
        const data = {}
        const homeData = {}
        if (eventName === 'currentStepRaisedToken') {
          data.totalTokenAmount = getFormat(event.returnValues.raisedTokenAmount, 0)
          data.remainingToken = getFormat(event.returnValues.totalRemainingToken, 0)
          // data.eachAddressLimit = getFormat(event.returnValues.remainingTokenForPersonal, 0)
          homeData.currentStepTokenAmount = getFormat(event.returnValues.raisedTokenAmount, 0)
        } else if (eventName === 'currentStepRaisedEther') {
          data.totalETHAmount = getFormat(event.returnValues.raisedETHAmount, 5)
          data.remainingETH = getFormat(event.returnValues.totalRemainingEther, 5)
          homeData.currentStepRaisedETH = getFormat(event.returnValues.raisedETHAmount, 5)
        }
        commit('GET_OFFER_INFO', data)
        commit('GET_CURRENT_STEP_FUNDS_INFO', homeData)
      })
      .on('data', (event) => {
        console.log(eventName, 'listen event on data:', event)
      })
      .on('changed', (event) => {
        console.log(eventName, 'listen event on changed:', event)
      })
      .on('error', console.error)
    },
  }
})
