<template>
  <div class="home">
    <el-row>
      <el-col class="col-flex-column first-column"  :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
        <div class="left">
          <div class="detail-area">
            <div v-for="(item, i) in detailList" :key="i" class="detail-area-item">
              <span>{{getText(i, 'detail')}}</span>
              <span>{{i === 2 || i === 4 ? 'Lv ' : ''}}{{userInfo[i]}}{{i === 3 ? ' ETH' : ''}}</span>
            </div>
          </div>
          <div class="detail-area">
            <div v-for="(item, i) in incomeList" :key="i" class="detail-area-item">
              <span>{{getText(i)}}</span>
              <span>{{userInfo[i+6]}} ETH</span>
              <span v-if="i === 2 || i === 5" @click="withdraw(i)" class="withdraw-button">提现</span>
            </div>
          </div>
        </div>
      </el-col>
      <el-col class="col-flex-column"  :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
        <div class="right">
          <div class="bet-area">
            <span class="title">所有投注</span>
            <div class="all">{{gameInfo.investMoney}} ETH</div>
            <div class="select-bet">
              <div class="select-bet-button"
                v-for="(item, i) in betList"
                @click="selectBetAmount(item)"
                :key="i"
              >
                <span>{{item}}ETH</span>
              </div>
            </div>
            <span class="bet-amount-text">您的金额</span>
            <div class="add-minus">
              <span class="minus" @click="minus">-</span>
              <input class="value" type="number" :placeholder="betAmount" v-model="betAmount" />
              <span class="add" @click="add">+</span>
            </div>
            <span class="wallet-remain">钱包余额：{{balance}} ETH</span>
            <div class="bet-button" @click="bet">
              <span>{{wait && isWait ? '排队中' : '投注'}}</span>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import store from '../store'
import { mapState, mapGetters, mapActions } from 'vuex'
import BigNumber from 'bignumber.js'

export default {
  name: 'home',
  components: {
  },
  data() {
    return {
      betAmount: 1,
      betList: [
        1,
        6,
        11,
        15
      ],
      detailList: [
        { cn: '邀请码', value: 23, en: 'Invite the number' },
        { cn: '推荐人', value: 23, en: 'Invitation amount' },
        { cn: '分红等级', value: 23, en: 'Investment amount' },
        { cn: '投注总额', value: 23, en: 'Raise investment amount' },
        { cn: '节点等级', value: 23, en: 'Lucky prize' },
        { cn: '推荐好友个数', value: 23, en: 'Fission reward' },
      ],
      incomeList: [
        { cn: '总收益', en: '', value: 0 },
        { cn: '当前持仓', en: '', value: 0 },
        { cn: '可提现数量', en: '', value: 0 },
        { cn: '累计投注分红', en: '', value: 0 },
        { cn: '累计节点收益', en: '', value: 0 },
        { cn: '待提现收益', en: '', value: 0 },
      ]
    }
  },
  computed: {
    ...mapState({
      account: state => state.account,
      balance: state => state.balance,
      userInfo: state => state.userInfo,
      gameInfo: state => state.gameInfo,
      isWait: state => state.isWait,
      wait: state => state.wait,
    })
  },
  methods: {
    withdraw(i) {
      console.log(i)
      if (i === 2) {
        store.dispatch('redeem')
      } else if (i === 5) {
        store.dispatch('withdrawProfit')
      }
    },
    async getInviteCode() {
      const inviteCode = Math.random().toString().substr(2, 8)
      const result = await store.dispatch('isCode', inviteCode)
      console.log('isCode', inviteCode, result)
      if (result) {
        return inviteCode
      }
      return false
    },
    async bet() {
      const urlReferrer =  this.$router.history.current.query.referrer
      console.log('url referrer ', urlReferrer)
      if (this.wait && this.isWait) {
        this.$alert('排队中...')
        return
      }
      let inviteCode = this.userInfo[0]
      while(!inviteCode) {
        inviteCode = await this.getInviteCode()
        console.log('in while loop', inviteCode)
      }
      const params = {
        inviteCode: inviteCode,
        referer: this.userInfo[1] || urlReferrer || '99999999',
        value: this.betAmount
      }
      store.dispatch('waitInvest', params)
    },
    selectBetAmount(value) {
      this.betAmount = value
      console.log(value)
    },
    minus() {
      console.log('minus')
      if (this.betAmount <= 0) {
        console.log('can not minus')
        return
      }
      this.betAmount -= 1
    },
    add() {
      console.log('add')
      this.betAmount = parseInt(this.betAmount) + 1
    },
    getText(index, type="income") {
      let listName = 'incomeList'
      if (type !== "income") {
        listName = 'detailList'
      }
      if (localStorage.getItem('currentLocale') && localStorage.getItem('currentLocale') == 'en') {
        return this[listName][index].en
      } else {
        return this[listName][index].cn
      }
    },
  },
  mounted() {
  },
  created() {
  }
}
</script>
<style lang='scss' scoped>
@import "../assets/variables.scss";

.home {
  font-size: 0.14rem;
  height: 100%;
  display: flex;
  justify-content: center;
  .el-row {
    width: 88%;
    display: flex;
    flex-wrap: wrap;
    .first-column {
      @media screen and (max-width: 560px){
        order: 1;
      }
    }
  }
  .left {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
    .detail-area {
      width: 53rem;
      height: 24rem;
      @media screen and (max-width: 560px){
        width: 80vw;
        height: 36.23vw;
      }
      background-image: url('~@/assets/image/bg-left.png');
      background-repeat: no-repeat;
      background-size: contain;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .detail-area-item {
        width: 81.13%;
        border-bottom: 1px solid #666;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 2rem;
        color: #fff;
        .withdraw-button {
          cursor: pointer;
          font-size: 1rem;
          width: 5rem;
          height: 2rem;
          background-image: url('~@/assets/image/withdraw-button.png');
          background-repeat: no-repeat;
          background-size: contain;
        }
      }
    }
  }
  .right {
    display: flex;
    justify-content: center;
    width: 100%;
    .bet-area {
      width: 32rem;
      height: 51rem;
      @media screen and (max-width: 560px){
        //width: 80vw;
        /* height: 134.2vw; */
        /* transform: scaleX(2); */
      }
      background-image: url('~@/assets/image/bg-bet.png');
      background-repeat: no-repeat;
      background-size: contain;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      font-size: 3rem;
      .title {
        margin-top: 6rem;
      }
      .all {
        display: flex;
        align-items: center;
      }
      .all:before {
        content: '';
        background-image: url('~@/assets/image/eth.png');
        background-size: 2rem 3rem;
        width: 2rem;
        height: 3rem;
      }
      .select-bet {
        display: flex;
        justify-content: space-between;
        width: 27rem;
        .select-bet-button {
          display: flex;
          justify-content: center;
          align-items: center;
          color: #000;
          font-size: 1.5rem;
          width: 6rem;
          height: 4rem;
          background-image: url('~@/assets/image/bet-button.png');
          background-repeat: no-repeat;
          background-size: cover;
          cursor: pointer;
          span {
            margin-top: .4rem;
          }
        }
      }
      .bet-amount-text {
        font-size: 1rem;
      }
      .add-minus {
        .add .minus {
          cursor: pointer;
          font-size: 3rem;
        }
        input {
          width: 10rem;
          border: none;
          border-bottom: solid 1px #fff;
          background-color: transparent;
          color: #fff;
          font-size: 5rem;
          text-align: center;
          margin-left: 2rem;
          margin-right: 2rem;
          appearance: none;
        }
        input::-webkit-inner-spin-button {
          -webkit-appearance: none;
          appearance: none;
          margin: 0;
        }
      }
      .wallet-remain {
        margin-top: 2rem;
        font-size: 1rem;
      }
      .bet-button {
        cursor: pointer;
        font-size: 4rem;
        text-shadow: 0 0 2rem $shadowColor;
        margin-top: 4rem;
        margin-bottom: 8rem;
        margin-left: 1rem;
      }
    }
  }
}
</style>
