<template>
  <div id="app">
    <el-container>
      <el-header>
        <div id="nav">
          <div class="left">
            <router-link class="logo" to="/">
              <img src="./assets/image/logo.png">
              <span>Super fair</span>
            </router-link>
          </div>
          <div class="right">
            <div class="invite" :data-clipboard-text="inviteUrl">
              <span>邀请好友</span>
            </div>
            <router-link class="rules" to="/rules">{{ $t('nav.rules') }}</router-link>
            <a target="_blank" class="email" href="mailto:support@hyperfair.io"></a>
            <a target="_blank" class="telegram" href="https://t.me/hyperfairgame"></a>
            <el-dropdown @command="handleCommand">
              <span class="el-dropdown-link">
                {{currentLanguage}}<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="cn">中文简体</el-dropdown-item>
                <el-dropdown-item command="en">English</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </div>
      </el-header>
      <div class="intro-area">
        <div class="intro-container">
        <span class="slogon">绝对公正的以太坊博弈游戏</span>
        <div class="detail">
          <span>规则简单</span>
          <span>公开透明</span>
          <span>代码开源</span>
        </div>
        </div>
      </div>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import Web3 from "web3"
import Clipboard from 'clipboard'
import store from './store'
import { mapState, mapGetters, mapActions } from 'vuex'
import TokenJson from '../../contract/build/contracts/SFtoken.json'
import SuperFair from '../../contract/build/contracts/SuperFair.json'

export default {
  name: 'app',
  data () {
    return {
      account: ethereum && ethereum.selectedAddress || '登录钱包',
      currentLanguage: localStorage.getItem('currentLocale') && localStorage.getItem('currentLocale') == 'en' ? 'English' : '中文简体',
      currentNetworkVersion:
        web3.currentProvider.connection && web3.currentProvider.connection.networkVersion || '3',
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.userInfo,
      inviteUrl: state => `${window.location.origin}/#/?referrer=${state.userInfo && state.userInfo[0]}`
    }),
  },
  components: {
  },
  methods: {
    handleCommand(command) {
      this._i18n.locale = command
      if (command === 'cn') {
        this.currentLanguage = '中文简体'
      } else if (command === 'en') {
        this.currentLanguage = 'English'
      }
      localStorage.setItem('currentLocale', command)
    },
    async initTokenContract() {
      const address = TokenJson["networks"][this.currentNetworkVersion].address
      const contract = await new web3.eth.Contract(
        TokenJson.abi,
        address
      )
      return contract
    },
    async initContract() {
      const address = SuperFair["networks"][this.currentNetworkVersion].address
      const contract = await new web3.eth.Contract(
        SuperFair.abi,
        address
      )
      return contract
    },
    async unlockMetaMask() {
      const self = this
      if (window.web3 && window.web3.currentProvider.isTrust) {
        window.web3 = new Web3(web3.currentProvider)
        const account = web3.eth.accounts[0]
        this.$alert(`trust`)
        self.account = account
      } else if (window.ethereum) {
        window.web3 = new Web3(ethereum)
        try {
          await ethereum.enable();
          self.account = ethereum.selectedAddress
        } catch (error) {
          self.$alert('您拒绝了授权使用 MetaMask', '提示', {
            confirmButtonText: '确定',
          })
        }
      } else if (window.web3) {
        window.web3 = new Web3(web3.currentProvider)
        const account = web3.eth.accounts[0]
        if (!account){
          self.$alert('您的 MetaMask 被锁住了，请先解锁', '提示', {
            confirmButtonText: '确定',
          })
          return
        }
        self.account = account
      } else {
        self.$alert('您的浏览器还没有安装 MetaMask，请先安装该插件', '提示', {
          confirmButtonText: '确定',
        })
        return
      }
      store.state.account = self.account
    }
  },
  async created () {
    await this.unlockMetaMask()
    const contract = await this.initContract()
    const tokenContract = await this.initTokenContract()
    window.contract = contract
    window.tokenContract = tokenContract
    store.dispatch('getGameInfo', contract)
    store.dispatch('getWaitInfo', contract)
    store.dispatch('getBalance', this.account)
    // const data = {contract: contract, eventName: 'currentStepRaisedToken'}
    // store.dispatch('startListenEvent', data)
  },
  mounted () {
    const self = this
    const clipboard = new Clipboard('.invite')
    clipboard.on('success', function(e) {
      console.log('click invite url', self.userInfo[0])
      if (self.userInfo[0]) {
        self.$alert(`已经成功复制您的邀请地址: ${self.inviteUrl}`, '提示', {
          confirmButtonText: '确定',
        })
      } else {
        self.$alert('参与投注获取邀请码')
      }
      e.clearSelection()
    })
    clipboard.on('error', function(e) {
      console.log(e)
    })
  }
}
</script>

<style lang='scss'>
@import "./assets/base.scss";
body {
  margin: 0;
}

html {
  font-size: 0.8333vw;
}

.el-dropdown-link {
  font-size: 2rem;
}

.el-dropdown {
  display: flex;

}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100vh;
  background-image: url('~@/assets/image/bg-index.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}

.el-header, .el-footer {
  color: #fff;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 6rem !important;
  @media screen and (max-width: 560px){
    height: 60px !important;
  }
 }

.el-message-box {
  width: 80% !important;
}

.el-container {
  height: 100%;
  .intro-area {
    height: 10rem;
    width: 100%;
    display: flex;
    justify-content: center;
    .intro-container {
      width: 88%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      color: #fff;
      font-size: 3rem;
      .slogon {
        margin-left: 6.5rem;
        font-weight: bold;
        text-shadow: 0 1rem 1rem $shadowColor;
      }
      .detail {
        margin-top: 1rem;
        margin-left: 1.5rem;
        display: flex;
        font-size: 2rem;
        font-weight: bold;
        span {
          margin-left: 5rem;
        }
      }
    }
  }
  .el-main {
    height: 100%;
    color: #fff;
  }
}

#nav {
  display: flex;
  justify-content: space-between;
  font-size: 0.14rem;
  height: 100%;
  width: 88%;
  .locale-changer {
    color: #fff;
  }
  .left {
    display: flex;
    justify-content: space-around;
    color: #fff;
    .logo {
      display: flex;
      align-items: center;
      img {
        width: 2rem;
        height: 4rem;
      }
      span {
        margin-left: 2rem;
        font-size: 3rem;
        font-weight: bold;
      }
    }
  }
  .right {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 50%;
    font-size: 2rem;
    .invite {
      display: flex;
      cursor: pointer;
      justify-content: center;
      align-items: center;
      width: 10rem;
      height: 3rem;
      border-radius: 2rem;
      border: solid 1px #1598c7;
    }
    .email {
      width: 6rem;
      height: 6rem;
      background-image: url('~@/assets/image/email.png');
      background-repeat: no-repeat;
      background-size: contain;
    }
    .telegram {
      width: 6rem;
      height: 6rem;
      background-image: url('~@/assets/image/telegram.png');
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
}
.el-footer {
  color: #fff;
  line-height: .4rem;
}
::-webkit-scrollbar {
  width: 4px;
  height: 2px;
}

::-webkit-scrollbar-thumb {
  border-radius: 3px;
}
</style>
